* {
    margin: 0;
    padding: 0;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%; // 1 rem = 10px
}

body {
    position: relative;
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    line-height: 1.6;
}

#root {
    min-height: 100vh;
}

a {
    text-decoration: none;
    color: black;
}
